$color-main: #0056A9;
$color-red: #C23349;
$color-green: #5CAB5F;
$color-dark-blue: #32449F;
$color-menu-hover: #9ae7ff;
$color-white: #ececec;

body {
    background: black; }

.space_background {
    height: 135vh;
    min-height: 1100px;
    background: url("../images/193.png"), $color-main 50%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-blend-mode: screen;
    display: flex;
    flex-direction: column; }

.workday-page {
    margin: 0 auto;
    margin-top: 20px;
    width: 79vw;
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 20px;
    button {
        width: 18vw;
        padding: 5px 5px;
        color: $color-white;
        background-color: #00167981;
        border-style: solid;
        border-width: 2px;
        border-color: #4b6af5 #545454 #545454 #4b6af5;
        border-radius: 5px; }
    button:hover {
        background-color: #002cf2; }
    button:active {
        background-color: #0028db; }
    button:disabled {
        color: $color-white;
        background-color: #022eee;
        border-color: #4b6af5 #545454 #545454 #4b6af5; } }

.data-box {
    margin: 0 auto;
    margin-bottom: 20px;
    padding: 15px;
    min-height: 50px;
    width: 77vw;
    background-color: #8e8e8e;
    border-style: solid;
    border-width: 2px;
    border-color: #a2a2a2 #262626 #262626 #a2a2a2;
    border-radius: 6px;
    &__item {
        margin-right: 10px;
        padding: 3px;
        line-height: 20px;
        margin-bottom: 3px; //* отодвигаем строки вниз друг от друга */
        border: 1px solid #8a8a8a;
        border-radius: 3px;
        user-select: none; } }

.request-call-box {
    width: 40vw;
    height: 24px;
    margin: 0 auto;
    margin-bottom: 15px;
    button {
        width: 100%;
        height: 30px;
        color: $color-white;
        background: #7c2201;
        border-style: solid;
        border-width: 2px;
        border-color: #b66c51 #505050 #505050 #b66c51;
        border-radius: 5px; }
    button:hover {
        color: $color-white;
        background: #9c2a00;
        border-color: #b66c51 #505050 #505050 #b66c51; }
    button:active {
        background-color: #7c2201; } }

@keyframes myfirst {
    50% {
        background: red; } }

.dropdown-menu {
    --bs-dropdown-link-active-color: black;
    --bs-dropdown-link-active-bg: $color-white;
    a {
        text-decoration: none; } }
.telephone {
    padding: 4px 12px 4px 12px;
    width: 300px;
    display: flex;
    justify-content: space-between;
    align-items: center; }

.dropdown-item {}

.free {
    height: 26px;
    background: #e1e1e1;
    color: #000000;
    line-height: 16px;
    box-shadow: 1px 1px 2px #272727 inset; }
.busy {
    height: auto;
    background: #646464;
    color: #d8d8d8;
    box-shadow: 1px 1px 2px #272727 inset; }


.client-free {
    width: 250px;
    min-width: 155px;
    text-align: center; }
.client-busy {
    width: 250px;
    min-width: 155px; }

.time-workday {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    &__time {
        width: 65px;
        min-width: 50px;
        text-align: center; }
    &__time-free {}
    &__time-busy {}
    &__services {
        width: 1000px;
        padding-left: 5px;
        padding-right: 5px;
        height: auto; }
    &__cancel-button {
        width: 100px;
        height: 24px;
        padding: 1px;
        text-align: center;
        color: $color-white;
        background-color: #a32b00;
        border-style: solid;
        border-width: 2px;
        border-color: #b66c51 #505050 #505050 #b66c51;
        border-radius: 5px; }
    &__cancel-button:hover {
        background-color: #bd3402; }
    &__cancel-button:active {
        background-color: #a32b00; }
    &__fake_button {
        width: 100px;
        min-width: 100px;
        height: 24px; } }

.modal {
    --bs-modal-width: 350px; }

.modal-content {
    background-color: #4a4a4a; //var(--bs-modal-color)
    &__body-text {
        color: $color-white;
        font-size: 22px;
        text-align: center; } }

@media (max-width: 1199px) {
    .workday-page {
        button {
            font-size: 14px; } }
    .data-box {
        padding: 10px;
        &__item {
            font-size: 14px;
            line-height: 16px;
            padding: 3px;
            margin-right: 5px; }
        button {
            font-size: 14px; } }
    .telephone {
        width: 270px; }
    .time-workday {
        &__cancel-button {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 90px; }
        &__fake_button {
            width: 90px;
            min-width: 90px; } }
    .free {
        height: auto; } }

@media (max-width: 767px) {
    .space_background {
        height: 125vh; }
    .workday-page {
        button {
            font-size: 12px; } }
    .telephone {
        width: 240px; }
    .data-box {
        padding: 7px;
        &__item {
            font-size: 12px;
            line-height: 14px;
            padding: 2px;
            margin-right: 3px; }
        button {
            font-size: 12px; } }
    .client-free {
        min-width: 125px; }
    .client-busy {
        min-width: 125px; }
    .time-workday {
        &__handling-button {
            width: 80px; }
        &__cancel-button {
            padding: 0;
            width: 80px; }
        &__fake_button {
            min-width: 80px; }
        &__time {
            min-width: 40px; } } }

@media (max-width: 575px) {
    .workday-page {
        button {
            font-size: 10px; } }
    .data-box {
        &__item {
            font-size: 10px;
            line-height: 12px;
            padding: 2px;
            margin-right: 3px; }
        button {
            font-size: 10px; } }
    .client-free {
        min-width: 110px; }
    .client-busy {
        min-width: 110px; }
    .telephone {
        width: 200px;
        font-size: 10px; }
    .time-workday {
        &__cancel-button {
            font-size: 0px;
            height: 20px;
            width: 60px; }
        &__fake_button {
            min-width: 60px; }
        &__time {
            min-width: 35px; } } }

@media (max-width: 460px) {
    .workday-page {
        button {
            font-size: 8px; } }
    .telephone {
        width: 170px; }
    .time-workday {
        &__button {
            button {
                font-size: 0px; } }
        &__cancel-button {
            width: 35px; }
        &__fake_button {
            min-width: 35px; }
        &__time {
            min-width: 35px; } } }

@media (max-width: 391px) {
    .space_background {
        height: 135vh; }
    .data-box {
        &__item {
            font-size: 8px;
            line-height: 10px;
            padding: 2px;
            margin-right: 3px; } }
    .time-workday {
        &__button {
            button {
                font-size: 0px; } } } }

@media (max-width: 344px) {
    .space_background {
        height: 135vh; }
    .data-box {
        &__item {
            font-size: 7px;
            line-height: 8px;
            padding: 2px;
            margin-right: 3px; } }
    .time-workday {
        &__button {
            button {
                font-size: 0px; } }
        &__cancel-button {
            height: 20px;
            width: 30px; }
        &__fake_button {
            min-width: 30px; }
        &__time {
            min-width: 30px; } } }
